.container {
  margin: 0 auto;

  .review & {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .hero & {
    position: relative;
    height: 100%;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .container {
    max-width: 480px;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 34px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding: 0 20px;
  }
}
